import React, { useEffect, useState, lazy, useCallback } from "react";
import { useDispatch, useSelector } from "react-redux";
import { PayPalButton } from "react-paypal-button-v2";
import Axios from "axios";
import { useHistory } from "react-router-dom";
import { removeCartItem, fetchUserData } from "../action/userAction";
import { useFirestore } from "react-redux-firebase";
import { nodeApiLink } from "../config/NodeServerLink";
import { CartItem } from "./CartItem";
import { TransitionGroup } from "react-transition-group";
import Fade from "react-reveal/Fade";
import { Trans, useTranslation } from "react-i18next";
const EmptyCart = lazy(() => import("./EmptyCart"));

export const CartMain = () => {
    const userId = useSelector((state) => state.userStatus.userInfo && state.userStatus.userInfo._id);
    const userCart = useSelector((state) => state.userStatus.userInfo && state.userStatus.userInfo.cart);
    const firestore = useFirestore();
    const [cart, setCart] = useState([]);
    const history = useHistory();
    const dispatch = useDispatch();
    const { t } = useTranslation("translation",{useSuspense:false});
    useEffect(() => {
        const getCartItem = async () => {
            const { data } = await Axios.get(`${nodeApiLink}/api/user/cart`, { withCredentials: true });
            setCart(data);
        };
        getCartItem();
        return () => {
            //
        };
    }, [userCart]);
    
    const removeCartItemHandler = useCallback(
        (id) => {
            dispatch(removeCartItem(id));
        },
        [dispatch],
    )
    const paymentSuccessHandler = useCallback(
        async () => {
            await Axios.post(`${nodeApiLink}/api/user/buy`, {
                courses: cart.map((course) => {
                    return course._id;
                }),
            });
            history.push("/");
            await firestore.collection("noticfications").add({
                userId,
                title: "Mua hàng",
                description: `bạn đã mua ${cart.length} khoá học. Hãy đến học ngay nào`,
                Date: firestore.FieldValue.serverTimestamp(),
                seen: false,
            });
            dispatch(fetchUserData());
        },
        [cart, dispatch, firestore, history, userId],
    )
    return cart?.length > 0 ? (
        <div className="container">
            <div className="row">
                <div className="col-12">
                    <h3><Trans t={t}>Có {{inCartNum:cart.length}} khoá học trong giỏ</Trans></h3>
                </div>
            </div>
            <div className="row">
                <div className="col-12 col-md-9 my-2">
                    <div className="left" style={{ boxShadow: "0 0 2px #dedfe0" }}>
                        <TransitionGroup {...{ appear: true, enter: true, exit: true }}>
                            {cart.map((course) => {
                                return (
                                    <Fade key={course._id} duration={700} collapse bottom>
                                        <CartItem course={course} removeCartItemHandler={removeCartItemHandler} />
                                    </Fade>
                                );
                            })}
                        </TransitionGroup>
                    </div>
                </div>
                <div className="col-12 col-md-3">
                    <div className="right">
                        <div className="price-text">
                            <div className="total"><Trans t={t}>Tổng hóa đơn</Trans>:</div>
                            <div className="price-large">
                                {cart?.length
                                    ? new Intl.NumberFormat('vi-VN',{
                                        style:'currency',
                                        currency:'VND'
                                    }).format( cart.reduce((sum = 0, course) => {
                                          return sum + course.price;
                                      }, 0))
                                    : 0}
                            </div>
                            <div className="space"></div>
                            <PayPalButton
                                amount={
                                    cart?.length
                                        ? cart.reduce((sum = 0, course) => {
                                              return sum + course.price;
                                          }, 0)
                                        : 0
                                }
                                onSuccess={paymentSuccessHandler}
                            />
                        </div>
                    </div>
                </div>
            </div>
        </div>
    ) : (
        <EmptyCart />
    );
};
