import React, { useEffect } from "react";
import { CartMain } from "../Component/CartMain";
import { Link, useHistory } from "react-router-dom";
import { useSelector } from "react-redux";
import '../scssFile/not_empty.css'
import '../scssFile/empty.css'
import '../scssFile/cart-header.css'
import { useTranslation, Trans } from "react-i18next";

export const Cart = () => {
    const auth = useSelector((state) => state.userStatus.auth);
    const history = useHistory();
    const { t } = useTranslation("translation",{useSuspense:false});
    useEffect(() => {
        if (!auth) history.push("/");
        return () => {
            //
        };
    }, [auth, history]);
    return (
        <div className="content" style={{ minHeight: 800 }}>
            <div className="cart-header">
                <div className="container">
                    <div className="header-bar">
                        <div>
                            <ol className="breadcrumb">
                                <li className="breadcrumb-item">
                                    <Link to="/">
                                        <i className="fas fa-home"></i>
                                    </Link>
                                </li>
                                <li className="breadcrumb-item active" aria-current="page">
                                <Trans t={t}>Giỏ hàng</Trans>
                                </li>
                            </ol>
                            <h1><Trans t={t}>Thẻ mua sắm</Trans></h1>
                        </div>
                    </div>
                </div>
            </div>
            <div className="space">
                <div className="space-item"></div>
            </div>
            <CartMain />
        </div>
    );
};
export default Cart;
